* {
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif;
  box-sizing: border-box;
}

.row {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

a {
  color: white;
  text-decoration: none;
 }

a:hover { 
  color: black;
}

#textDescription {
  font-size: x-small;
}

video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
}

.content {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100%;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  overflow: auto;
  border: 2px solid white;
  color: white;
  text-align: center;
}

.content2 {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 100%;
  top: 0;
  background: rgba(0, 0, 0, 0.5);
  overflow: auto;
  border: 2px solid white;
  color: white;
}

#back {
  font-size: 1.5em;
}

.border1{
  border: 5px solid transparent;
}

.border2 {
  border: 20px solid transparent;
  text-align: center;
}

.border3 {
  border: 1px solid transparent;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  overflow: auto;
  margin-left: 6%;
  margin-right: 6%;
  margin-bottom: 6%;
}

.border4 {
  border: 20px solid transparent;
}

/********** Large devices only **********/
@media (min-width: 992px) {
  .content, .content3 {
    font-size: 4em;
  }

  .container1 {
    display: flex;
  }

  .container2 {
    border: 20px solid transparent;
    font-size: 4em;
  }
  
  .container3 {
    border: 20px solid transparent;
  }
  
  .col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12 {
    float: left;
  }
  .col-lg-1 {
